						</el-option>
					</el-select>
				</div>
			</div>
			<!--      <div style="clear: both"></div>
      -->
			<div class="headerRight">
				<div class=searchAndrest>
					<SolidButton :type='"solid"' :text='"查询"' @click="search"></SolidButton>
					<SolidButton :type='"hollow"' :text='"重置"' @click="reset"></SolidButton>
				</div>
			</div>
			<!--2、列表头部-->
			<div class="clearfix" style="padding-top: 20px;height: auto;">
				<Button :type='adds.type' :icon='adds.icon' :text='adds.text' @click="add"></Button>
				<Button :type='edits.type' :icon='edits.icon' :text='edits.text' @click="edit"></Button>
				<Button :type='out.type' :icon='out.icon' :text='out.text' @click="toExamine"></Button>
			</div>
		</div>

		<div class="flexTable">
			<div class="d-box v-box">
				<div class="flexTable spaceTable">
