						<el-table ref="multipleTable" stripe :data="tableData" tooltip-effect="dark" style="width: 100%;" height="100%" @selection-change="handleSelectionChange" @row-click="toggleSelection">
							<el-table-column type="selection" width="55">
							</el-table-column>
							<el-table-column prop="custname" label="账户名" align="center">
							</el-table-column>
							<el-table-column prop="linktel" label="手机号" align="center">
							</el-table-column>
							<el-table-column prop="custtype" label="客户类别" align="center">
								<template slot-scope="scope">
									<span v-if="scope.row.custtype=='1'">安吉物流</span>
									<span v-if="scope.row.custtype=='2'">主机厂</span>
									<span v-if="scope.row.custtype=='3'">经销商</span>
								</template>
							</el-table-column>
							<el-table-column prop="brandName" label="所属主体" align="center">
							</el-table-column>
							<el-table-column prop="" label="经销商" align="center" :formatter="distributor">
							</el-table-column>
							<el-table-column prop="authflag" label="审核状态" align="center">
								<template slot-scope="scope">
									<span v-if="scope.row.authflag=='0'">申请待审核</span>
									<span v-if="scope.row.authflag=='1'">审核通过</span>
									<span v-if="scope.row.authflag=='2'">审核不通过</span>
								</template>
							</el-table-column>
						</el-table>
					</div>
				</div>
				<div class="footerDiv">
					<el-pagination style="text-align: center;padding: 6px 0px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage4" :page-size="10" layout="total,sizes, prev,pager, next,jumper" :total="total">
					</el-pagination>
				</div>
			</div>
		</div>

		<!--新增编辑弹出框-->
		<el-dialog :title="title" center :visible.sync="plus" width='640px'>
			<el-form :model="form">
				<el-form-item label="账户名" :label-width="formLabelWidth">
					<el-input class="" v-model="form.name" placeholder="请输入账户名" auto-complete="off" :disabled="disable"></el-input>
				</el-form-item>
				<el-form-item label="手机号" :label-width="formLabelWidth">
					<el-input v-model="form.phone" placeholder="请输入手机号" auto-complete="off" :disabled="disable"></el-input>
				</el-form-item>
				<el-form-item label="客户类别" :label-width="formLabelWidth">
					<el-select v-model="customerType" placeholder="请选择" @change="customerTypeSelect" :disabled="disable">
						<el-option v-for="item in customerList" :key="item.custType" :label="item.custName" :value="item.custType">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="所属主体" :label-width="formLabelWidth">
					<el-select filterable v-model="form.addSubject" placeholder="请选择" @change="selctMain" :disabled=trueOrfalse>
						<el-option v-for="item in optionsMain" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
        <el-form-item v-show="shouOrHied" label="是否签收" :label-width="formLabelWidth" id="nihao">
          <el-select v-model="form.WhetherSignOrNot" placeholder="请选择">
            <el-option v-for="item in WhetherSignOrNotList" :key="item.custType" :label="item.custName" :value="item.custType">
            </el-option>
          </el-select>
        </el-form-item>
				<el-form-item class="dealerChoice" id="dealerChoice" label="经销商" :label-width="formLabelWidth">
					<el-select :disabled=trueOrfalses class="dealerChoiceselct" v-model="form.newDealer" multiple filterable remote reserve-keyword placeholder="请输入经销商名称" :remote-method="remoteMethod" :loading="loading" clearable>
						<el-option v-for="item in newOptions" :key="item.name" :label="item.name" :value="item.idCode">
						</el-option>
					</el-select>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<SolidButton :type='"solid"' :text='"确定"' @click="confirm"></SolidButton>
				<SolidButton :type='"hollow"' :text='"取消"' @click="plus = false"></SolidButton>
			</div>
		</el-dialog>
		<!--审核弹出框-->
		<el-dialog title="审核状态" center :visible.sync="dialogVisible" width="360px">
			<span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="reviewPass" class="submitStyle">审核通过</el-button>
        <el-button @click="noReviewPass" class="cancelStyle">审核不通过</el-button>
  </span>
		</el-dialog>
	</div>
</template>

<script>
	import Button from '../../button/Button.vue';
	import SolidButton from '../../SelfControls/solidButton/solidButton.vue';

	export default {
		components: {
			Button,
			SolidButton,
		},
		data() {
			return {
        shouOrHied:false,
			  userId:"",//用户id
			  subjectCode:"",//所属主体
        subjectCodeSearch:"",//所属主体
				adds: {
					text: '新增',
					type: 'icon-add',
					icon: 'el-icon-plus',
				},
				edits: {
					text: '编辑',
					type: 'icon-edit',
					icon: 'el-icon-edit',
				},
				out: {
					text: '查看',
					type: 'icon-search',
					icon: 'el-icon-search',
				},
        WhetherSignOrNotList:[{
          custType:0,
          custName:"不可签收"
        },{
          custType:1,
          custName:"可签收"
        }
        ],
				dialogVisible: false, //审核弹框
				savetype: "",
				editId: "", //编辑的id
				disable: false, //form表单的禁用
				reqParams: { //查询区的变量
					name: '',
					phone: '',
					subject: '',
					id: '',
					examineId: ""
				},
				loading: false,
				options4: [], //经销商模糊搜索数据数组
				options5: [], //经销商模糊搜索数据数组
				currentPage4: 1,
				options: [{
					value: '0',
					label: '申请待审核'
				}, {
					value: '1',
					label: '审核通过'
				}, {
					value: '2',
					label: '审核不通过'
				}],
				customerType: "",
				customerTypeSearch: "",
				trueOrfalse: false,
				trueOrfalses: false,
				customerList: [],//客户类别数组
				optionsMain: [], //所属主体数组
        optionsMainSearch: [], //所属主体数组
				tableData: [], //表格数据
				currentPage1: 5,
				total: 0, //总数
				clickPageNum: '1',
				clickPagerows: '10',
				newOptions: [],
				form: { //新增编辑数据
					name: '',
					phone: '',
					addSubject: '',
					// addreqParam: '',
					newDealer: [],
          WhetherSignOrNot:"",//是否可签收
				},
				temporaryId: '', //编辑存的经销商ID
				formLabelWidth: '74px',
				title: '', //弹出框的title
				plus: false,
				system: [],
				list: [], //点击复选框选中的数据
				setupDealer: [], //设置按钮经销商数组
				subjectIdarray: []
			}
		},
    watch:{
		  "plus":function(val){
		    var self=this;
		    if(val==false){
          self.shouOrHied=false;
          self.trueOrfalses = false;
          self.trueOrfalse = false;
        }
      }
    },
		methods: {
			// 选择不同的客户类别，禁用不同的所属主体与经销商
			customerTypeSelect(value) {
				var self = this;
				if(value == 2) {
					// alert("主机厂")
          self.shouOrHied=false;
					self.trueOrfalses = true;
					self.trueOrfalse = false;
					self.form.newDealer = [];
					self.newOptions = [];
          self.form.WhetherSignOrNot="";
				} else if(value == 1) {
					// alert("安吉物流")
          self.shouOrHied=false;
					self.trueOrfalses = true;
					self.trueOrfalse = true;
					self.form.addSubject = "";
					self.form.newDealer = [];
					self.newOptions = [];
          self.form.WhetherSignOrNot="";

				} else if(value == 3) {
					// alert("经销商")
          self.shouOrHied=true;
					self.trueOrfalses = false;
					self.trueOrfalse = false;
          self.form.WhetherSignOrNot="";

				}
			},
      //客户类别下拉数据
      customerTypeSele(){
			  var self=this;
        self.$api.post("crm/customer/getByUserId",{id:self.userId},respose => {
          self.customerList=respose.repData
        },error => {

        })
      },
      selctMainSearch(value){
        var self = this;
        self.options5=[];
        self.reqParams.id="";
        for(var item of self.optionsMainSearch){
          if(value==item.id){
            self.subjectCodeSearch=item.code
          }
        }
      },
			// 选择不同主体，清空经销商
			selctMain(value) {
				var self = this;
				for(var item of self.optionsMain){
				  if(value==item.id){
            self.subjectCode=item.code
          }
        }
				// self.form.addreqParam="";
				// self.options4=[];
				self.newOptions = [];
				self.form.newDealer = [];
			},
			// 表格主体翻译
			// subjectTranslation(row, column, cellValue) {
			// 	if(cellValue == "LSV") {
			// 		return "上汽大众"
			// 	} else if(cellValue == "LSG") {
			// 		return "上海通用"
			// 	} else if(cellValue == "SMC") {
			// 		return "上汽汽车"
			// 	} else if(cellValue == "YVI") {
			// 		return "沃尔沃"
			// 	}
			// },
      distributor(row, column, cellValue){
        if(row.type==1){
          return row.anjiDealerName;
        }else {
          return row.anjiWarehouseName;
        }
      },
			// 审核
			toExamine() {
				var self = this;
				var idLength = self.list.length;
				self.subjectIdarray = [];
				// if(idLength==1 && self.list[0].authflag==0){
				//   self.dialogVisible=true;
				// }else {
				//   this.$message({
				//     message: idLength==1 ? '只有未审核状态才能被审核' :'请选择一条数据',
				//     type: 'error'
				//   });
				//   return;
				// }
				if(idLength >= 1) {
					self.dialogVisible = true;
				} else {
					this.$message({
						message: '未勾选数据',
						type: 'warnig'
					});
					return;
				}
				for(var i = 0; i < self.list.length; i++) {
					self.subjectIdarray.push(self.list[i].id.toString())
				}
			},
			// 审核通过按钮
			reviewPass() {
				var self = this;
				self.auditMethod(1)
			},
			// 审核不通过
			noReviewPass() {
				var self = this;
				self.auditMethod(2)
			},
			auditMethod(authId) {
				var self = this;
				self.$api.post('crm/customer/checkCustomer', {
					ids: self.subjectIdarray,
					authflag: authId
				}, res => {
					self.dialogVisible = false;
					this.$message({
						message: '成功',
						type: 'success'
					});
					self.loadData();
				}, error => {
					self.$message.error(error.repMsg)
				})
			},
			// 查询
			search() {
				var self = this;
				self.loadData();
			},
			// 重置
			reset() {
				var self = this;
				self.reqParams.name = "";
				self.reqParams.phone = "";
				self.reqParams.subject = "";
				self.subjectCodeSearch = "";
				self.reqParams.id = "";
				self.reqParams.examineId = "";
				self.customerType = "";
				self.customerTypeSearch = "";
				self.loadData();
			},
      // 模糊搜索
      remoteMethods(query) {
        var self = this;
        // self.searchJinxiao="search"
        if(query !== '') {
          this.loading = true;
          setTimeout(() => {
            self.$api.post('crm/anjiDealer/getList', {
              code:self.subjectCodeSearch,
              customerCode:self.subjectCodeSearch,
              customerId: self.reqParams.subject,
              name: query,
              pageNum: 1,
              pageRows: 10
            }, res => {
              if(res.repData.list.length > 0) {
                // self.options4 = res.repData.list
                // self.options5 = res.repData.list
                self.options5 = res.repData.list
              } else {
                // this.reqParams.missionName = "";
                // this.options4 = [];
                // this.options5 = [];
                self.options5 = [];
              }
              this.loading = false
            }, error => {
              self.$message.error('请求出错')
            })
          }, 200);
        } else {
          self.newOptions = [];
          // this.options4 = [];
          // this.options5 = [];
        }
      },
			// 模糊搜索
			remoteMethod(query) {
				var self = this;
        // self.searchJinxiao="search"
				if(self.form.addSubject == "" && self.plus==true) {
					this.$message.warning('请选择所属主体');
					return;
				}
				if(query !== '') {
					this.loading = true;
					setTimeout(() => {
						self.$api.post('crm/anjiDealer/getList', {
						  code:self.subjectCode,
						  customerCode:self.subjectCode,
              customerId: self.form.addSubject,
              name: query,
							pageNum: 1,
							pageRows: 10
						}, res => {
							if(res.repData.list.length > 0) {
								// self.options4 = res.repData.list
								// self.options5 = res.repData.list
								self.newOptions = res.repData.list
							} else {
								// this.reqParams.missionName = "";
								// this.options4 = [];
								// this.options5 = [];
								self.newOptions = [];
							}
							this.loading = false
						}, error => {
							self.$message.error('请求出错')
						})
					}, 200);
				} else {
					self.newOptions = [];
					// this.options4 = [];
					// this.options5 = [];
				}
			},
			// 点击复选框获取当前行数据
			handleSelectionChange(val) {
				var self = this;
				self.list = val;
				console.log(self.list)
			},
			// 点击行选中复选框
			toggleSelection(row) {
				var rows = [];
				rows.push(row);
				if(rows) {
					rows.forEach(row => {
						this.$refs.multipleTable.toggleRowSelection(row);
					});
				} else {
					this.$refs.multipleTable.clearSelection();
				}
			},
			//初始化数据
			loadData() {
				var self = this;
				var p = {
					pageNum: self.clickPageNum,
					pageRows: self.clickPagerows,
					custtype: self.customerTypeSearch,
					custname: self.reqParams.name,
					linktel: self.reqParams.phone,
					brandCode: self.subjectCodeSearch,
					dealerid: self.reqParams.id,
					authflag: parseInt(self.reqParams.examineId)
				};
				self.$api.post('crm/customer/getAllCustomer', p, r => {
					if(r.repData == null) {
						this.$message.error('数据加载失败，请刷新页面再试');
						return;
					}
					self.tableData = r.repData.list;
					self.total = r.repData.total;
				}, e => {
					console.log(e);
				})
			},
			subjectData() {
				var self = this;
				self.$api.post('crm/anjiCustomer/queryList', {}, res => {
					self.optionsMain = res.repData.list
					self.optionsMainSearch = res.repData.list
				}, error => {
					console.log(error)
				})
			},
			handleSizeChange(val) {
				var self = this;
				// self.clickPageNum = val;
				self.clickPagerows = val
				this.loadData();
			},
			handleCurrentChange(val) {
				var self = this;
				self.clickPageNum = val;
				this.loadData();
			},
			//新增
			add() {
				var self = this;
				self.savetype = "add";
				self.disable = false;
				this.title = '新建';
				this.plus = true;
				self.form.name = "";
				self.form.phone = "";
				self.form.addSubject = "";
				self.customerType = "";
				self.form.newDealer = [];
				self.newOptions = [];
        self.form.WhetherSignOrNot="";
				// self.form.addreqParam = "";
				// self.options4=[];
			},
			edit() {
				var self = this;
				self.disable = false;
				self.form.name = "";
        self.form.WhetherSignOrNot="";
				self.form.phone = "";
				self.form.addSubject = "";
				self.customerType = ""
				// self.form.addreqParam = "";
				// self.options4=[];
				self.form.newDealer = [];
				self.newOptions = [];
				self.setupDealer = [];
				self.title = '编辑';
				self.savetype = "edit";
				var idLength = self.list.length;
				if(idLength == 1) {
					self.customerType = self.list[0].custtype;
					// alert(self.list[0].custtype)
					self.form.name = self.list[0].custname;
					self.form.phone = self.list[0].linktel;
					// self.form.addSubject = self.list[0].brandCode;//所属主体id
          self.subjectCode=self.list[0].brandCode;//所属主体code
          if(self.customerType==3){
            self.shouOrHied=true;
          }
          if(self.list[0].canReceive==0){
            self.shouOrHied=false;
          }
          if(self.list[0].canReceive==1){
            self.shouOrHied=true;
          }
          self.form.WhetherSignOrNot=self.list[0].canReceive;
          for(var item of self.optionsMain){
            if(self.list[0].brandCode==item.code){
              self.form.addSubject=item.id
            }
          }
					// self.options4.unshift({"id":self.list[0].dealerid,"dealername":self.list[0].dealername});
					// self.form.addreqParam=self.list[0].dealerid;
					self.editId = self.list[0].id;
					self.plus = true;
          // code:self.subjectCode,
          //   customerCode:self.subjectCode,
          //   customerId: self.form.addSubject,
          //   name: query,
					self.$api.post('crm/customer/getByPrimaryKey', {
						id: self.list[0].id
					}, res => {
						for(var i = 0; i < res.repData.crmAuthList.length; i++) {
							self.form.newDealer.push(res.repData.crmAuthList[i].idCode)
						}
