			confirm() {
				var self = this;
				// var type= !isNaN(Number(self.form.addreqParam))
				var para = {
					custname: self.form.name,
					linktel: self.form.phone,
					brandCode: self.subjectCode,//所属主体
          dealerIdCodes: self.form.newDealer,
					custtype: self.customerType,//客户类别
          canReceive:self.form.WhetherSignOrNot,
					id: self.editId
				}
				// var par={
				//   id:self.editId,
				//   dealerIds:self.setupDealer
				// }
				var dealerLength = self.form.newDealer.length
				// alert(self.trueOrfalses)
				if(self.form.name == "" || self.form.phone == "" || self.customerType == "" || (self.trueOrfalse == false && self.form.addSubject == "") || (self.trueOrfalses == false && self.form.newDealer.length == 0)) {
					self.$message.warning('请完善信息');
					return;
				}
				self.$api.post(self.savetype == "add" ? 'crm/customer/insertCustomer' : 'crm/customer/updateCustomer', para, res => {
					self.plus = false;
					self.loadData();
					this.$message({
						message: self.savetype == "add" ? '新增成功' : '编辑成功',
						type: 'success'
					});
				}, error => {
					self.$message.error('请求出错')
				})
			}
